body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.yfm_constructor {
  color: var(--g-color-text-primary);
  font-family: var(--g-font-family-sans)
}

.yfm_constructor code,.yfm_constructor kbd,.yfm_constructor pre {
  color: var(--g-color-text-primary);
  font-family: var(--g-font-family-monospace)
}

.yfm_constructor.yfm_constructor h1,.yfm_constructor.yfm_constructor h2,.yfm_constructor.yfm_constructor h3,.yfm_constructor.yfm_constructor h4,.yfm_constructor.yfm_constructor h5,.yfm_constructor.yfm_constructor h6,.yfm_constructor.yfm_constructor li,.yfm_constructor.yfm_constructor p,.yfm_constructor.yfm_constructor span {
  color: var(--g-color-text-primary)
}

.yfm_constructor.yfm_constructor_notice li,.yfm_constructor.yfm_constructor_notice p {
  color: var(--g-color-text-secondary)
}

.yfm_constructor.yfm_constructor_meta_info p {
  color: var(--pc-media-card-meta-info-color);
  font-weight: var(--g-text-accent-font-weight)
}

.yfm_constructor.yfm_constructor li,.yfm_constructor.yfm_constructor ol,.yfm_constructor.yfm_constructor p,.yfm_constructor.yfm_constructor ul {
  margin: 0 0 12px
}

.yfm_constructor.yfm_constructor p strong {
  font-weight: var(--g-text-accent-font-weight)
}

.yfm_constructor.yfm_constructor_size_s li,.yfm_constructor.yfm_constructor_size_s ol,.yfm_constructor.yfm_constructor_size_s p,.yfm_constructor.yfm_constructor_size_s ul {
  margin-bottom: 8px
}

.yfm_constructor.yfm_constructor_theme_dark p {
  color: var(--g-color-text-light-primary)
}

.yfm_constructor_list_style ul {
  padding-left: 20px
}

.yfm_constructor_list_style_dash ul {
  list-style-type: none;
  position: relative
}

.yfm_constructor_list_style_dash ul li:before {
  content: "";
  margin-left: -20px;
  position: absolute
}

.yfm_constructor a {
  border-radius: var(--g-focus-border-radius);
  color: var(--g-color-text-link);
  cursor: pointer;
  outline: none;
  text-decoration: none
}

.utilityfocus .yfm_constructor a:focus {
  outline: 2px solid #ffdb4d
}

.yfm_constructor a:active,.yfm_constructor a:hover {
  --pc-text-header-color: var(--g-color-text-link-hover);
  color: var(--g-color-text-link-hover)
}

.yfm_constructor a:focus {
  box-shadow: 0 0 0 2px var(--g-color-line-focus)
}

.yfm_constructor a:focus:not(:focus-visible) {
  box-shadow: none
}

.yfm_constructor table {
  background: var(--g-color-base-background);
  border: 1px solid var(--g-color-line-generic);
  color: var(--g-color-text-primary)
}

.yfm_constructor table tr:nth-child(2n),.yfm_constructor thead {
  background-color: var(--g-color-base-generic)
}

.yfm_constructor_table table {
  border: 0;
  table-layout: fixed
}

.yfm_constructor_table thead,.yfm_constructor_table tr {
  background-color: transparent!important
}

.yfm_constructor_table tbody tr {
  border-top: 1px solid var(--g-color-line-generic)
}

.yfm_constructor_table td,.yfm_constructor_table th {
  box-sizing: content-box;
  min-width: 120px;
  white-space: inherit!important;
  width: 120px
}

.yfm_constructor_table td:first-child,.yfm_constructor_table th:first-child {
  min-width: 172px;
  width: 172px
}

.yfm_constructor_table th {
  text-align: left!important
}

.dc-doc-page .yfm {
  --yfm-color-hljs-background: var(--g-color-base-background);
  --yfm-color-hljs-subst: var(--g-color-text-complementary);
  --yfm-color-hljs-comment: var(--g-color-text-secondary);
  --yfm-color-hljs-deletion: var(--g-color-line-danger);
  --yfm-color-hljs-section: var(--g-color-line-danger);
  color: var(--g-color-text-primary)
}

.dc-doc-page .yfm ::-webkit-scrollbar,.dc-doc-page .yfm::-webkit-scrollbar {
  background: transparent;
  height: var(--g-scrollbar-width)
}

.dc-doc-page .yfm ::-webkit-scrollbar-track,.dc-doc-page .yfm::-webkit-scrollbar-track {
  background: transparent
}

.dc-doc-page .yfm a {
  border-radius: var(--g-focus-border-radius);
  color: var(--g-color-text-link)
}

.dc-doc-page .yfm a:active,.dc-doc-page .yfm a:hover {
  color: var(--g-color-text-link-hover)
}

.dc-doc-page .yfm a:focus {
  box-shadow: 0 0 0 2px var(--g-color-line-focus);
  outline: 0
}

.dc-doc-page .yfm a:focus:not(:focus-visible) {
  box-shadow: none
}

.dc-doc-page .yfm a.yfm-anchor:focus:before {
  visibility: visible
}

.dc-doc-page .yfm img {
  background-color: var(--g-color-base-background)
}

.dc-doc-page .yfm.yfm-accent-info {
  background: var(--g-color-base-info-light)
}

.dc-doc-page .yfm.yfm-accent-tip {
  background: var(--g-color-base-positive-light)
}

.dc-doc-page .yfm.yfm-accent-alert {
  background: var(--g-color-base-danger-light)
}

.dc-doc-page .yfm.yfm-accent-warning {
  background: var(--g-color-base-warning-light)
}

.dc-doc-page .yfm code {
  background: var(--g-color-base-misc-light);
  color: var(--g-color-text-misc);
  font-size: var(--dc-code-short-font-size);
  line-height: var(--dc-code-short-line-height);
  padding: var(--dc-code-short-padding)
}

.dc-doc-page .yfm pre>code {
  background: var(--g-color-base-misc-light);
  color: var(--g-color-text-complementary);
  font-size: var(--dc-code-font-size);
  line-height: var(--dc-code-line-height);
  padding: 16px
}

.dc-doc-page .yfm dfn {
  background: var(--g-color-base-float);
  box-shadow: 0 8px 20px var(--g-color-sfx-shadow)
}

.dc-doc-page .yfm dfn:before {
  box-shadow: 0 0 0 1px var(--g-color-line-generic-solid)
}

.dc-doc-page .yfm table {
  background: var(--g-color-base-background);
  border-color: var(--g-color-line-generic);
  color: var(--g-color-text-primary)
}

.dc-doc-page .yfm table thead,.dc-doc-page .yfm table tr:nth-child(2n) {
  background: var(--g-color-base-generic)
}

.dc-doc-page .yfm hr {
  background-color: var(--g-color-base-background)
}

.dc-doc-page .yfm blockquote {
  border-left-color: var(--g-color-base-brand)
}

.dc-doc-page .yfm .yfm-tab-list {
  border-bottom-color: var(--g-color-line-generic)
}

.dc-doc-page .yfm .yfm-tab {
  border-radius: var(--g-focus-border-radius)
}

.dc-doc-page .yfm .yfm-tab:active,.dc-doc-page .yfm .yfm-tab:hover {
  color: var(--g-color-text-link-hover)
}

.dc-doc-page .yfm .yfm-tab:focus {
  outline: 2px solid var(--g-color-line-focus);
  outline-offset: 0
}

.dc-doc-page .yfm .yfm-tab:focus:not(:focus-visible) {
  outline: 0
}

.dc-doc-page .yfm .yfm-tab.active {
  border-bottom-color: var(--g-color-base-brand)
}

.dc-doc-page .yfm .yfm-cut {
  margin-bottom: 15px
}

.dc-doc-page .yfm .yfm-cut.open .yfm-cut-content {
  padding: 5px 0 0 30px
}

.dc-doc-page .yfm .yfm-term_title:hover {
  color: var(--g-color-text-link-hover)
}

.dc-doc-page .yfm h1,.dc-doc-page .yfm h2 {
  margin-bottom: 20px;
  margin-top: 25px;
  margin-top: calc(25px - var(--dc-header-height, 0px));
  padding-top: 0;
  padding-top: calc(var(--dc-header-height, 0px))
}

.dc-doc-page .yfm h3,.dc-doc-page .yfm h4,.dc-doc-page .yfm h5,.dc-doc-page .yfm h6 {
  margin-top: 17px;
  margin-top: calc(17px - var(--dc-header-height, 0px));
  padding-top: 0;
  padding-top: calc(var(--dc-header-height, 0px))
}

.dc-doc-page .yfm h1+h2,.dc-doc-page .yfm h1:first-child,.dc-doc-page .yfm h2+h3,.dc-doc-page .yfm h2:first-child,.dc-doc-page .yfm h3+h4,.dc-doc-page .yfm h3:first-child,.dc-doc-page .yfm h4+h5,.dc-doc-page .yfm h4:first-child,.dc-doc-page .yfm h5+h6,.dc-doc-page .yfm h5:first-child,.dc-doc-page .yfm h6:first-child {
  margin-top: 0;
  margin-top: calc(0px - var(--dc-header-height, 0px));
  padding-top: 0;
  padding-top: calc(var(--dc-header-height, 0px))
}

.dc-doc-page .yfm .yfm-page__delimeter {
  background: var(--g-color-line-generic);
  border-radius: 2px;
  height: 4px;
  margin-bottom: 0;
  margin-top: 20px
}

.dc-doc-page .yfm .yfm-page__delimeter+h1,.dc-doc-page .yfm .yfm-page__delimeter+h2,.dc-doc-page .yfm .yfm-page__delimeter+h3,.dc-doc-page .yfm .yfm-page__delimeter+h4,.dc-doc-page .yfm .yfm-page__delimeter+h5,.dc-doc-page .yfm .yfm-page__delimeter+h6 {
  margin-top: 40px;
  margin-top: calc(40px - var(--dc-header-height, 0px));
  padding-top: 0;
  padding-top: calc(var(--dc-header-height, 0px))
}

.dc-doc-page .yfm .yfm-original-link:before {
  content: none
}

.dc-doc-page .yfm .yfm-original-link svg {
  visibility: hidden
}

.dc-doc-page .yfm h1 .yfm-original-link:before,.dc-doc-page .yfm h2 .yfm-original-link:before,.dc-doc-page .yfm h3 .yfm-original-link:before,.dc-doc-page .yfm h4 .yfm-original-link:before,.dc-doc-page .yfm h5 .yfm-original-link:before,.dc-doc-page .yfm h6 .yfm-original-link:before {
  content: none
}

.dc-doc-page .yfm h1 .yfm-original-link svg,.dc-doc-page .yfm h2 .yfm-original-link svg,.dc-doc-page .yfm h3 .yfm-original-link svg,.dc-doc-page .yfm h4 .yfm-original-link svg,.dc-doc-page .yfm h5 .yfm-original-link svg,.dc-doc-page .yfm h6 .yfm-original-link svg {
  visibility: hidden
}

.dc-doc-page .yfm h1:hover .yfm-original-link svg,.dc-doc-page .yfm h2:hover .yfm-original-link svg,.dc-doc-page .yfm h3:hover .yfm-original-link svg,.dc-doc-page .yfm h4:hover .yfm-original-link svg,.dc-doc-page .yfm h5:hover .yfm-original-link svg,.dc-doc-page .yfm h6:hover .yfm-original-link svg {
  visibility: visible
}

.dc-doc-page .yfm .yfm-anchor_hidden {
  visibility: hidden
}

.dc-doc-page .yfm .yfm-anchor_hidden:before {
  visibility: hidden!important
}

