.yfm-sandbox__column {
  display: flex;
  flex-direction: column
}

.yfm-sandbox__card-text {
  display: block;
  padding: 16px;
  text-align: initial
}

.yfm-sandbox__card-wrapper {
  position: relative
}

.yfm-sandbox__card {
  overflow-x: auto
}

.yfm-sandbox__loader-container {
  align-items: center;
  display: flex;
  height: 300px;
  justify-content: center
}

.yfm-sandbox .yfm-sandbox__pre {
  margin: 0
}

.yfm-sandbox .yfm-sandbox__possible-reasons {
  margin-top: 15px
}

.yfm-sandbox__clipboard-button {
  display: none;
  position: absolute;
  right: 16px;
  top: 16px
}

.yfm-sandbox__card-wrapper:hover .yfm-sandbox__clipboard-button {
  display: block
}

.openapi {
  --dc-openapi-methods-post: #2d8b5d;
  --dc-openapi-methods-put: #30aa6e;
  --dc-openapi-methods-get: #348bdc;
  --dc-openapi-methods-patch: #bd8e4b;
  --dc-openapi-methods-delete: #e9033a;
  --dc-openapi-status-code-ok: #30aa6e;
  --dc-openapi-status-code-redirect: #348bdc;
  --dc-openapi-status-code-client: #e9033a;
  --dc-openapi-status-code-server: #ffbe5c
}

.openapi .openapi-description-annotation {
  color: var(--g-color-text-hint);
  font-style: normal
}

.openapi__required {
  color: var(--g-color-text-danger)
}

.openapi #request {
  margin-top: 0
}

.openapi .openapi__deprecated {
  text-decoration: line-through
}

.openapi>.openapi__request__wrapper:not(:last-child) {
  margin-bottom: 0
}

.openapi .openapi__requests {
  align-items: flex-start;
  display: flex;
  flex-direction: column
}

.openapi .openapi__request {
  align-items: center;
  display: inline-flex;
  margin-bottom: 5px;
  max-width: 100%;
  overflow-y: scroll
}

.openapi .openapi__request__wrapper {
  display: inline-block;
  max-width: 100%;
  position: relative
}

.openapi .openapi__request * {
  margin: 0
}

.openapi .openapi__request>.openapi__method {
  background-color: var(--method);
  border-radius: 5px 0 0 5px;
  color: #fff;
  padding: 8px 16px
}

.openapi .openapi__request>.yfm-clipboard {
  border: 2px solid var(--method);
  border-radius: 0 5px 5px 0;
  overflow: visible;
  position: static;
  white-space: nowrap
}

.openapi .openapi__request>.yfm-clipboard code {
  padding: 6px 20px 6px 6px
}

.openapi .openapi__request>.yfm-clipboard code,.openapi .openapi__request>.yfm-clipboard pre {
  border-radius: 0;
  overflow: visible
}

.openapi .openapi__request>.yfm-clipboard svg {
  display: block;
  opacity: 0;
  position: absolute;
  right: 10px;
  top: 10px;
  transition: opacity .1s
}

.openapi .openapi__request>.yfm-clipboard:hover svg {
  opacity: 1
}

.openapi h3 {
  transition: color .1s
}

.openapi .openapi__request__description {
  font-size: 1.1em;
  margin: 20px 0 0
}

.openapi h2[id^="2"] {
  color: var(--dc-openapi-status-code-ok)
}

.openapi h2[id^="3"] {
  color: var(--dc-openapi-status-code-redirect)
}

.openapi h2[id^="4"] {
  color: var(--dc-openapi-status-code-client)
}

.openapi h2[id^="5"] {
  color: var(--dc-openapi-status-code-server)
}

[class^=openapi__response__code__]>:last-child {
  margin-bottom: 15px
}

*,:after,:before {
  box-sizing: inherit
}

body,html {
  box-sizing: border-box;
  margin: 0;
  padding: 0
