  color: var(--g-color-text-hint);
}
.g-hotkey_view_dark {
  background-color: var(--g-color-base-light-simple-hover);
  color: var(--g-color-text-light-complementary);
}
.g-hotkey_view_dark .g-hotkey__plus {
  color: var(--g-color-text-light-hint);
