.yfm {
  word-wrap: break-word;
  --yfm-color-hljs-background: rgba(107, 132, 153, 0.12);
  --yfm-color-hljs-subst: #444;
  --yfm-color-hljs-comment: #888;
  --yfm-color-hljs-deletion: #800;
  --yfm-color-hljs-section: #800;
  --yfm-color-hljs-pseudo: #bc6060;
  --yfm-color-hljs-literal: #78a960;
  --yfm-color-hljs-addition: #397300;
  --yfm-color-hljs-meta: #1f7199;
  --yfm-color-hljs-meta-string: #4d99bf;
  --yfm-font-family-monospace: "Menlo", "Monaco", "Consolas", "Ubuntu Mono",
    "Liberation Mono", "DejaVu Sans Mono", "Courier New", "Courier", monospace;
  --yfm-font-family-sans: "Segoe UI", Arial, Helvetica, sans-serif,
    "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  color: rgba(0, 0, 0, 0.7);
  font-family: Segoe UI, Arial, Helvetica, sans-serif, Apple Color Emoji,
    Segoe UI Emoji, Segoe UI Symbol;
  font-family: var(--yfm-font-family-sans);
  font-size: 15px;
  line-height: 20px;
}
.yfm * {
  box-sizing: border-box;
}
.yfm > :not(h2):not(h3):not(h4):not(h5):not(h6):first-child {
  margin-top: 0 !important;
}
.yfm > :last-child {
  margin-bottom: 0 !important;
}
.yfm h1,
.yfm h2,
.yfm h3,
.yfm h4,
.yfm h5,
.yfm h6 {
  font-weight: 500;
  margin-bottom: 15px;
}
.yfm h1 {
  font-size: 32px;
  line-height: 40px;
}
.yfm h2 {
  font-size: 24px;
  line-height: 28px;
}
.yfm h3 {
  font-size: 20px;
}
.yfm h3,
.yfm h4 {
  line-height: 24px;
}
.yfm h4 {
  font-size: 17px;
}
.yfm h5 {
  font-size: 15px;
  line-height: 20px;
}
.yfm h6 {
  font-size: 13px;
  line-height: 18px;
}
.yfm a {
  background-color: transparent;
  color: #027bf3;
  -webkit-text-decoration: none;
  text-decoration: none;
}
.yfm a:active,
.yfm a:hover {
  color: #004080;
}
.yfm a:not([href]) {
  color: inherit;
}
.yfm strong {
  font-weight: 700;
}
.yfm img,
.yfm svg {
  border: none;
  box-sizing: content-box;
  max-width: 100%;
  vertical-align: middle;
}
.yfm img[align="right"],
.yfm svg[align="right"] {
  padding-left: 20px;
}
.yfm img[align="left"],
.yfm svg[align="left"] {
  padding-right: 20px;
}
.yfm img {
  background-color: #fff;
  -o-object-fit: contain;
  object-fit: contain;
}
.yfm .katex svg,
.yfm svg {
  height: auto;
}
.yfm img + small {
  display: block;
}
.yfm blockquote,
.yfm dl,
.yfm ol,
.yfm p,
.yfm pre,
.yfm table,
.yfm ul {
  margin: 0 0 15px;
}
.yfm dl pre,
.yfm ol pre,
.yfm ul pre {
  margin-top: 15px;
}
.yfm code,
.yfm kbd,
.yfm pre {
  font-feature-settings: normal;
  font-family: var(--yfm-font-family-monospace);
  font-size: 1em;
}
.yfm input {
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
  margin: 0;
  overflow: visible;
}
.yfm input[type="checkbox"] {
  padding: 0;
  top: 1px;
}
.yfm input[type="checkbox"],
.yfm table {
  box-sizing: border-box;
  position: relative;
}
.yfm table {
  background: #fff;
  border: 1px solid rgba(0, 0, 0, 0.07);
  border-collapse: collapse;
  border-radius: 8px;
  border-spacing: 0;
  color: #333;
  display: inline-block;
  max-width: 100%;
  overflow: auto;
  width: auto;
  z-index: 1;
}
.yfm table thead,
.yfm table tr:nth-child(2n) {
  background-color: rgba(0, 0, 0, 0.02);
}
.yfm table tr:first-child td,
.yfm table tr:first-child th {
  border-top: none;
}
.yfm table tr:last-child td,
.yfm table tr:last-child th {
  border-bottom: none;
}
.yfm table td,
.yfm table th {
  border: 1px solid rgba(0, 0, 0, 0.07);
  overflow: hidden;
  padding: 10px 20px;
  text-overflow: ellipsis;
}
.yfm table td > :first-child,
.yfm table th > :first-child {
  margin-top: 0;
}
.yfm table td > :last-child,
.yfm table th > :last-child {
  margin-bottom: 0;
}
.yfm table td {
  vertical-align: top;
  white-space: normal;
}
.yfm table th {
  font-weight: 500;
  text-align: left;
}
.yfm table tr td:first-child,
.yfm table tr th:first-child {
  border-left: none;
}
.yfm table tr td:last-child,
.yfm table tr th:last-child {
  border-right: none;
}
.yfm hr {
  background-color: rgba(0, 0, 0, 0.07);
  border: none;
  box-sizing: content-box;
  height: 0.25em;
  margin: 1.5em 0;
  overflow: hidden;
  padding: 0;
}
.yfm hr:after,
.yfm hr:before {
  content: "";
  display: table;
}
.yfm hr:after {
  clear: both;
}
.yfm blockquote {
  border-left: 3px solid #027bf3;
  padding-left: 12px;
  position: relative;
}
.yfm blockquote > :first-child {
  margin-top: 0;
}
.yfm blockquote > :last-child {
  margin-bottom: 0;
}
.yfm dl {
  padding: 0;
}
.yfm dl > :first-child {
  margin-top: 0;
}
.yfm dl > :last-child {
  margin-bottom: 0;
}
.yfm dl dt {
  font-size: 1em;
  font-style: italic;
  font-weight: 500;
  margin-bottom: 5px;
}
.yfm dl dd {
  margin-bottom: 15px;
  padding: 0 15px;
}
.yfm dd {
  margin-left: 0;
}
.yfm dd > dd:first-child,
.yfm dd > ol:first-child,
.yfm dd > ul:first-child {
  padding-left: 0;
}
.yfm ol,
.yfm ul {
  padding-left: 2.2em;
}
.yfm ol ol,
.yfm ol ul,
.yfm ul ol,
.yfm ul ul {
  margin-bottom: 0;
  margin-top: 0;
}
.yfm:not(.yfm_no-list-reset) ol {
  counter-reset: list;
  list-style-type: none;
}
.yfm:not(.yfm_no-list-reset) ol > li {
  counter-increment: list;
  position: relative;
}
.yfm:not(.yfm_no-list-reset) ol > li:before {
  content: counters(list, ".") ". ";
  margin-right: 4px;
  position: absolute;
  right: 100%;
}
.yfm li {
  word-wrap: break-word;
}
.yfm li + li {
  margin-top: 0.33em;
}
.yfm li blockquote,
.yfm li p {
  margin-top: 15px;
}
.yfm code {
  background-color: rgba(107, 132, 153, 0.12);
  border-radius: 4px;
  color: #3b6080;
  font-size: 0.875em;
  padding: 0.1em 0.4em 0.15em;
  white-space: normal;
}
.yfm pre {
  word-wrap: normal;
}
.yfm table code {
  white-space: nowrap;
}
.yfm pre * code {
  word-wrap: normal;
  background-color: transparent;
  border: 0;
  color: rgba(0, 0, 0, 0.7);
  display: inline;
  margin: 0;
  overflow: visible;
  padding: 0;
}
.yfm pre > code {
  background-color: rgba(107, 132, 153, 0.12);
  color: rgba(0, 0, 0, 0.7);
  display: block;
  overflow: auto;
  padding: 16px;
  white-space: pre;
}
.yfm sub,
.yfm sup {
  line-height: 0;
}
.yfm .visually-hidden {
  clip: rect(0 0 0 0);
  border: 0;
  clip-path: inset(100%);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
  white-space: nowrap;
  width: 1px;
}
.yfm .yfm-note {
  border-radius: 10px;
  margin: 20px 0;
  max-width: 1296px;
  padding: 20px 20px 20px 64px;
}
.yfm .yfm-note .yfm-note-title {
  font-weight: 700;
}
.yfm .yfm-note > p {
  margin: 0 0 10px;
}
.yfm .yfm-note > p:first-child:before {
  box-sizing: content-box;
  display: block;
  float: left;
  height: 24px;
  margin-left: -44px;
  margin-top: -2px;
  padding-right: 20px;
  width: 24px;
}
.yfm .yfm-note > p:last-child {
  margin-bottom: 0;
}
.yfm .yfm-note.yfm-accent-info > p:first-child:before {
  content: "";
}
.yfm .yfm-note.yfm-accent-tip > p:first-child:before {
  content: "";
}
.yfm .yfm-note.yfm-accent-alert > p:first-child:before {
  content: "";
}
.yfm .yfm-note.yfm-accent-warning > p:first-child:before {
  content: "";
}
.yfm .yfm-note.yfm-accent-info {
  background: rgba(2, 123, 243, 0.08);
}
.yfm .yfm-note.yfm-accent-tip {
  background: rgba(63, 201, 46, 0.1);
}
.yfm .yfm-note.yfm-accent-alert {
  background: rgba(235, 50, 38, 0.08);
}
.yfm .yfm-note.yfm-accent-warning {
  background: rgba(255, 136, 0, 0.15);
}
.yfm h1 .yfm-anchor,
.yfm h2 .yfm-anchor,
.yfm h3 .yfm-anchor,
.yfm h4 .yfm-anchor,
.yfm h5 .yfm-anchor,
.yfm h6 .yfm-anchor {
  display: inline-block;
  float: left;
  font-size: 18px;
  margin-left: -24px;
  padding-right: 4px;
  text-align: center;
  width: 24px;
}
.yfm h1 .yfm-anchor:focus:before,
.yfm h2 .yfm-anchor:focus:before,
.yfm h3 .yfm-anchor:focus:before,
.yfm h4 .yfm-anchor:focus:before,
.yfm h5 .yfm-anchor:focus:before,
.yfm h6 .yfm-anchor:focus:before {
  opacity: 1;
}
.yfm h1 .yfm-anchor:before,
.yfm h2 .yfm-anchor:before,
.yfm h3 .yfm-anchor:before,
.yfm h4 .yfm-anchor:before,
.yfm h5 .yfm-anchor:before,
.yfm h6 .yfm-anchor:before {
  content: "#";
  opacity: 0;
}
.yfm h1:hover .yfm-anchor:before,
.yfm h2:hover .yfm-anchor:before,
.yfm h3:hover .yfm-anchor:before,
.yfm h4:hover .yfm-anchor:before,
.yfm h5:hover .yfm-anchor:before,
.yfm h6:hover .yfm-anchor:before {
  opacity: 1;
}
.yfm table h1 .yfm-anchor,
.yfm table h2 .yfm-anchor,
.yfm table h3 .yfm-anchor,
.yfm table h4 .yfm-anchor,
.yfm table h5 .yfm-anchor,
.yfm table h6 .yfm-anchor {
  margin-left: -1em;
  padding-right: 0;
  width: 1em;
}
.yfm .yfm-tabs .yfm-anchor:before {
  padding-right: 2px;
  position: absolute;
}
.yfm .hljs {
  background: rgba(107, 132, 153, 0.12);
  background: var(--yfm-color-hljs-background);
  display: block;
  overflow-x: auto;
  padding: 16px;
}
.yfm .hljs,
.yfm .hljs-subst {
  color: #444;
  color: var(--yfm-color-hljs-subst);
}
.yfm .hljs-comment {
  color: #888;
  color: var(--yfm-color-hljs-comment);
}
.yfm .hljs-attribute,
.yfm .hljs-doctag,
.yfm .hljs-keyword,
.yfm .hljs-meta-keyword,
.yfm .hljs-name,
.yfm .hljs-selector-tag {
  font-weight: 700;
}
.yfm .hljs-deletion,
.yfm .hljs-number,
.yfm .hljs-quote,
.yfm .hljs-selector-class,
.yfm .hljs-selector-id,
.yfm .hljs-string,
.yfm .hljs-template-tag,
.yfm .hljs-type {
  color: #800;
  color: var(--yfm-color-hljs-deletion);
}
.yfm .hljs-section,
.yfm .hljs-title {
  color: #800;
  color: var(--yfm-color-hljs-section);
  font-weight: 700;
}
.yfm .hljs-link,
.yfm .hljs-regexp,
.yfm .hljs-selector-attr,
.yfm .hljs-selector-pseudo,
.yfm .hljs-symbol,
.yfm .hljs-template-variable,
.yfm .hljs-variable {
  color: #bc6060;
  color: var(--yfm-color-hljs-pseudo);
}
.yfm .hljs-literal {
  color: #78a960;
  color: var(--yfm-color-hljs-literal);
}
.yfm .hljs-addition,
.yfm .hljs-built_in,
.yfm .hljs-bullet,
.yfm .hljs-code {
  color: #397300;
  color: var(--yfm-color-hljs-addition);
}
.yfm .hljs-meta {
  color: #1f7199;
  color: var(--yfm-color-hljs-meta);
}
.yfm .hljs-meta-string {
  color: #4d99bf;
  color: var(--yfm-color-hljs-meta-string);
}
.yfm .hljs-emphasis {
  font-style: italic;
}
.yfm .hljs-strong {
  font-weight: 700;
}
.yfm-clipboard {
  position: relative;
}
.yfm-clipboard:hover .yfm-clipboard-button {
  opacity: 1;
}
.yfm-clipboard > pre {
  border-radius: 10px;
  overflow: hidden;
}
.yfm-clipboard-button {
  background: none;
  border: none;
  color: inherit;
  cursor: pointer;
  font: inherit;
  opacity: 0;
  padding: 0;
  position: absolute;
  right: 16px;
  top: 16px;
  z-index: 1;
}
.yfm-clipboard-button:focus {
  opacity: 1;
}
.yfm-clipboard-icon {
  pointer-events: none;
}
.yfm-file__icon {
  ---yfm-file-icon-img: var(--yfm-file-icon, "");
  background-color: currentColor;
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: 100%;
  color: rgba(0, 0, 0, 0.7);
  color: var(--yfm-file-icon-color, rgba(0, 0, 0, 0.7));
  display: inline-block;
  margin: 0 2px;
  -webkit-mask-image: "";
  mask-image: "";
  -webkit-mask-image: var(---yfm-file-icon-img);
  mask-image: var(---yfm-file-icon-img);
  -webkit-mask-position: center;
  mask-position: center;
  -webkit-mask-repeat: no-repeat;
  mask-repeat: no-repeat;
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
  width: 16px;
}
.yfm-file__icon:before {
  content: "\a0";
}
.yfm-term_title {
  border-bottom: 1px dotted;
  color: #027bf3;
  cursor: pointer;
  font-size: inherit;
  font-style: normal;
  line-height: inherit;
}
.yfm-term_title:hover {
  color: #004080;
}
.yfm-term_dfn {
  background-color: #fff;
  border-radius: 4px;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.15);
  font-size: inherit;
  font-style: normal;
  line-height: inherit;
  max-width: 450px;
  opacity: 0;
  outline: none;
  padding: 10px;
  position: absolute;
  visibility: hidden;
  width: -moz-fit-content;
  width: fit-content;
  z-index: 100;
}
@media screen and (max-width: 600px) {
  .yfm-term_dfn {
    max-width: 80%;
  }
}
.yfm-term_dfn:before {
  border-radius: inherit;
  bottom: 0;
  box-shadow: 0 0 0 1px #e5e5e5;
  content: "";
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  z-index: -1;
}
.yfm-term_dfn.open {
  animation-duration: 0.1s;
  animation-fill-mode: forwards;
  animation-name: popup;
  animation-timing-function: ease-out;
  visibility: visible;
}
@keyframes popup {
  0% {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.yfm table td.cell-align-bottom-left,
.yfm table td.cell-align-top-left {
  text-align: left;
}
.yfm table td.cell-align-center,
.yfm table td.cell-align-top-center {
  text-align: center;
}
.yfm table td.cell-align-bottom-right,
.yfm table td.cell-align-top-right {
  text-align: right;
}
.yfm table td.cell-align-top-center,
.yfm table td.cell-align-top-left,
.yfm table td.cell-align-top-right {
  vertical-align: top;
}
.yfm table td.cell-align-center {
  vertical-align: middle;
}
.yfm table td.cell-align-bottom-left,
.yfm table td.cell-align-bottom-right {
  vertical-align: bottom;
}
.wide-container {
  align-items: center;
  display: flex;
  height: 100vh;
  justify-content: center;
  left: 0;
  position: fixed;
  top: 0;
  width: 100vw;
  z-index: 200;
}
.wide-container.dc-doc-page {
  max-width: 100% !important;
}
.wide-container .wide-content-viewer {
  visibility: hidden;
}
.wide-container .wide-content-wrapper {
  background-color: var(--g-color-base-background);
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  height: 90vh;
  width: 70vw;
  z-index: 200;
}
.wide-container .wide-content-wrapper .wide-content {
  height: 95%;
  width: 100%;
}
.wide-container .wide-content-wrapper .wide-content .wide-inner-element {
  height: 100%;
  max-width: 100%;
  width: 100%;
}
.wide-container .wide-content-wrapper .wide-content table {
  border-radius: 0;
}
.wide-container .wide-content-wrapper .wide-content .wide-thead-content {
  display: table;
  width: 100%;
}
.wide-container .wide-content-wrapper .wide-toolbar {
  align-items: center;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 10px 12px;
  width: 100%;
}
.wide-container .wide-content-wrapper .wide-entity-name {
  font-size: 18px;
  margin: 0;
}
.wide-container .wide-content-wrapper .wide-actions {
  align-self: flex-end;
}
.wide-container .wide-content-wrapper .wide-actions .close-action {
  border-radius: 3px;
  cursor: pointer;
  transition: background 0.3s;
}
.wide-container .wide-content-wrapper .wide-actions .close-action:hover {
  background: var(--g-color-base-simple-hover);
}
.wide-element-wrapper {
  display: flex;
  gap: 30px;
  position: relative;
}
.wide-element-wrapper:hover > .wide-content-viewer {
  visibility: visible;
}
.wide-element-wrapper .wide-inner-element {
  max-width: 700px;
}
.wide-content-viewer {
  box-sizing: content-box;
  color: var(--g-color-text-primary);
  cursor: pointer;
  height: 30px;
  padding: 6px;
  visibility: hidden;
  width: 30px;
  z-index: 100;
}
.wide-content-viewer > svg {
  height: 20px;
  width: 20px;
}
.wide-content-overlay {
  background-color: #000;
  height: 100vh;
  left: 0;
  opacity: 0.6;
  position: fixed;
  top: 0;
  width: 100vw;
  z-index: 100;
}
.yfm-cut {
  margin-bottom: 15px;
  transition: all 0.15s;
}
.yfm-cut.cut-highlight {
  background-clip: content-box;
  background-color: var(--g-color-base-simple-hover);
  border-radius: 3px;
  box-shadow: 0 3px 0 7px var(--g-color-base-simple-hover);
}
.yfm-cut-title {
  cursor: pointer;
  list-style: none;
  padding: 5px 0 5px 30px;
  position: relative;
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
}
.yfm-cut-title::-webkit-details-marker {
  display: none;
}
.yfm-cut-title:focus {
  outline-color: red;
}
.yfm-cut-title:before {
  background-image: "";
  content: "";
  height: 20px;
  left: 0;
  position: absolute;
  top: 50%;
  transform: translateY(-50%) rotate(-90deg);
  transition: transform 0.3s ease;
  width: 20px;
  z-index: 1;
}
.yfm-cut-content {
  padding: 5px 0 15px 30px;
}
.yfm-cut.open > .yfm-cut-title:before {
  transform: translateY(-50%);
}
.yfm:not(.yfm_no-list-reset) .yfm-cut ol {
  counter-reset: cut-list;
}
.yfm:not(.yfm_no-list-reset) .yfm-cut ol > li {
  counter-increment: cut-list;
}
.yfm:not(.yfm_no-list-reset) .yfm-cut ol > li:before {
  content: counters(cut-list, ".") ". ";
}
.yfm-tabs {
  margin-bottom: 15px;
}
.yfm:not(.yfm_no-list-reset) .yfm-tabs ol {
  counter-reset: tabs-list;
}
.yfm:not(.yfm_no-list-reset) .yfm-tabs ol > li {
  counter-increment: tabs-list;
}
.yfm:not(.yfm_no-list-reset) .yfm-tabs ol > li:before {
  content: counters(tabs-list, ".") ". ";
}
.yfm-tab-list {
  border-bottom: 1px solid rgba(0, 0, 0, 0.07);
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 15px;
}
.yfm-tabs-vertical > .yfm-tab-list {
  border-bottom: initial;
  flex-direction: column;
}
.yfm-tabs-vertical > .yfm-tab-panel {
  margin-left: 30px;
}
.yfm-tabs-vertical > .yfm-tab-group {
  display: flex;
  flex-direction: row;
  margin-bottom: 5px;
}
.yfm-tabs-vertical .yfm-vertical-tab > input[type="radio"] {
  height: 0;
  visibility: hidden;
