.g-modal {
  -webkit-overflow-scrolling: touch;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  background-color: var(--g-color-sfx-veil);
  display: none;
  inset: 0;
  margin: -9999px 0 0 -9999px;
  overflow: auto;
  position: fixed;
  visibility: hidden;
  z-index: 1000;
}
.g-modal__content-aligner {
  align-items: center;
  display: inline-flex;
  justify-content: center;
  min-height: 100%;
  min-width: 100%;
}
.g-modal__content-wrapper {
  margin: var(--g-modal-margin, 20px);
  overflow-x: hidden;
}
.g-modal__content,
.g-modal__content-wrapper {
  border-radius: var(--g-modal-border-radius, 5px);
}
.g-modal__content {
  background-color: var(--g-color-base-modal);
}
.g-modal__content_has-scroll {
  max-height: calc(100vh - var(--g-modal-margin, 20px) * 2);
  overflow-y: auto;
}
.g-modal,
.g-modal__content {
  animation-fill-mode: forwards;
  animation-timing-function: ease-out;
  outline: none;
}
.g-modal_exit_active,
.g-modal_open {
  display: block;
  margin: 0;
  visibility: visible;
}
.g-modal_appear_active,
.g-modal_enter_active {
  animation-duration: 0.15s;
  animation-name: g-modal-open;
}
.g-modal_appear_active .g-modal__content,
.g-modal_enter_active .g-modal__content {
  animation-duration: 0.15s;
  animation-name: g-modal-content-open;
}
.g-modal_exit_active {
  animation-duration: 0.2s;
  animation-name: g-modal;
}
@keyframes g-modal {
  0% {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
@keyframes g-modal-open {
  0% {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes g-modal-content-open {
  0% {
    transform: scale(0.75);
  }
  to {
    transform: scale(1);
  }
}
